.about {
    padding-top: 61px;
    padding-bottom: 50px;

    @media screen and (min-width: $tablet-min) {
        padding-top: 60px;
        padding-bottom: 86px;
    }

    @media screen and (min-width: $desktop-min) {
        padding-top: 60px;
        padding-bottom: 99px;
    }
}


.about__label {
    font-family: $font-family-primary;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.22;
    text-align: center;
    text-transform: uppercase;
    color: $text-color-secondary;
    margin-bottom: 12px;

@media screen and (min-width: $tablet-min) {
    font-weight: 800;
    font-size: 22px;
    line-height: 1.23;
    letter-spacing: 0.04em;
    }

@media screen and (min-width: $desktop-min) {
    font-size: 30px;
    }
}


.about__title {
    font-family: $font-family-secondary;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.27;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $text-color-secondary;
    margin-bottom: 32px;

    @media screen and (min-width: $tablet-min) {
        font-size: 34px;
        line-height: 1.26;
        letter-spacing: 0.06em;
        width: auto;
        margin-bottom: 72px;
    }

    @media screen and (min-width: $desktop-min) {
        font-size: 36px;
        line-height: 1.25;
        margin-bottom: 82px;
    }
}


.about__wrapper {
    @media screen and (min-width: $tablet-min) {
        display: flex;
        flex-direction: row;
        gap: 38px;
    }
    
    @media screen and (min-width: $desktop-min) {
        gap: 109px;
    }
}


.about__img-wrapper {
    position: relative;
    background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
    box-shadow: 0px 4.48583px 16.8218px rgba(153, 114, 23, 0.25);
    border-radius: 140px;
    width: 278px;
    height: 279px;
    margin: 0 auto 40px;
        
    @media screen and (min-width: $tablet-min) {
        box-shadow: 0px 5.30813px 19.9055px rgba(153, 114, 23, 0.25);
        border-radius: 165px;
        width: 330px;
        height: 330px;
        margin-bottom: 0;
    }
    
    @media screen and (min-width: $desktop-min) {
        box-shadow: 0px 8px 30px rgba(153, 114, 23, 0.25);
        border-radius: 250px;
        width: 490px;
        height: 490px;
        margin-bottom: 0;
    }
}


.about__picture {
    display: block;

    @media screen and (min-width: $tablet-min) {
        width: 330px;
    }

    @media screen and (min-width: $desktop-min) {
        width: 490px;
    }        
}


.about__img {
    position: absolute; 
    top: 22px;

    @media screen and (min-width: $tablet-min) {
        top: 26px;
    }
    
    @media screen and (min-width: $desktop-min) {
        top: 39px;
    }
}


.about__text-button-wrapper {
    text-align: center;

    @media screen and (min-width: $tablet-min) {
        display: flex;
        flex-direction: column;
        padding-top: 8px;
    }

    @media screen and (min-width: $desktop-min) {
        padding-top: 90px;
        padding-right: 105px;
    }
}


.about__text--first {
    font-weight: 400;
    font-size: 12px;
    line-height: 176%;
    text-align: center;
    letter-spacing: 0.04em;
    color: $text-color-secondary;
    margin-bottom: 4px;
    
    @media screen and (min-width: $tablet-min) {
        margin-bottom: 8px;
        text-align: start;
    }

    @media screen and (min-width: $desktop-min) {
        font-size: 16px;
    }
}


.about__text--second {
    font-weight: 400;
    font-size: 12px;
    line-height: 162%;
    text-align: center;
    color: $text-color-secondary;

    @media screen and (max-width: $mobile-adaptive-max) {
        margin-bottom: 10px;
    }
                
    @media screen and (min-width: $tablet-min) {
        margin-bottom: 8px;
        line-height: 176%;    
        letter-spacing: 0.04em;
        text-align: start;
    }

    @media screen and (min-width: $desktop-min) {
        font-size: 14px;
        line-height: 194%;
    }  
}


.about__text--third {
    @media screen and (max-width: $mobile-adaptive-max) {
        display: none;
    }

    @media screen and (min-width: $tablet-min) {
        font-size: 12px;
        line-height: 194%;
        letter-spacing: 0.04em;
        color: $text-color-secondary;
        margin-bottom: 32px;
        text-align: start;
    }

    @media screen and (min-width: $desktop-min) {
        font-size: 14px;
        margin-bottom: 54px;
    }
}

.about__button {
    width: 164px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;

    @media screen and (min-width: $tablet-min) {
        margin: 0;
    }
}


.about__button-icon {
    margin-left: 4px;
}